/**
 * == BackDrop Support ==
 */
 @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
	.content-wrapper {
		&.show-content {
			color: #e5e5e5;
			background-color: rgba(14, 21, 40, 0.6);
			backdrop-filter: blur(20px) saturate(120%);
			border: 1px solid rgba(14, 21, 40, .075);
		}

		.content-inner-wrapper {
			&::-webkit-scrollbar-track {
				background-color: rgba(4, 13, 21, 0.1);
			}
		}

		.content-header {
			background-color: rgba(14, 21, 40, 0.2);
		}
		.content-title {
			color: #fff;
			text-shadow: none;
		}
	}

	.main-menu {
		.nav-title {
            background-color: rgba(71, 87, 145, 0.32);
			backdrop-filter: blur(5px);
		}
	}

	.social-media li a {
		text-shadow: 1px 2px rgba(15, 15, 15, 0.7);
	}

	.skill-item {
		.skill-title {
			color: #fdbcc9;
		}
	}

	.portfolio {
		.work-card {
			background-color: rgba(4, 13, 21, .2);

			.work-title a {
				color: #fdbcc9;
			}
			.work-meta a {
				color: #fff;
			}
		}
	}

	.experience {
		.job-detail {
			.designation {
				color: #fdbcc9;
			}
			.work-duration {
				color: #d4d2d2;
			}
		}
	}
	
}
